.news {
    display: grid;
    gap: 30px;
    grid-template-columns: repeat(4, 1fr);
    position: relative;
    grid-template-rows: repeat(5, 110px);
    padding-bottom: 116px;
    @include media($md) {
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(10, 110px);
        padding-bottom: 85px;
    }
    @include media($xs) {
        row-gap: 30px;
        column-gap: 0;
        grid-template-columns: 1fr;
        grid-template-rows: repeat(14, 110px);
        padding-bottom: 55px;
    }

    .new {
        position: relative;
        max-width: 100%;
        height: 100%;
        img {
            max-width: 100%;
            height: 100%;
            @include media($md) {
                width: 100%;
            }
        }
    }
    &__img {
        object-fit: cover;
        position: absolute;
        
    }

    &__title {
        color: $white;
        font-size: 22px;
        line-height: 27px;
        font-weight: 700;
        margin-top: 83px;
        margin-left: 20px;
        @include limit(2);
    }

    &__text {
        color: $light;
        font-size: 18px;
        line-height: 22px;
        font-weight: 400;
        margin-top: 13px;
        margin-left: 20px;
        @include limit(4);
    }

    &__date {
        color: $white;
        font-size: 16px;
        line-height: 19px;
        font-weight: 400;
        margin-top: 23px;
        margin-left: 20px;
    }

    &__inf {
        color: $dark;
        padding: 10px 15px;
        background: $white;
        border-radius: 8px;
        position: absolute;
        top: 20px;
        left: 20px;
    }

    &__content {
        position: absolute;
        width: 100%;
        height: 100%;
        background: $newsBlock;
        &:hover {
            background: linear-gradient(180deg, rgba(0, 0, 0, 0.75) 0%, rgba(85, 97, 134, 0.5) 100%);
        }
    }

    .title1 {
        margin-top: 90px;
    }

    .text1 {
        margin-top: 20px;
    }

    .date1 {
        margin-top: 100px;
    }

    .new1 {
        grid-column: 1/3;
        grid-row: 1/4;
        @include media($md) {
            max-width: 100%;
            grid-column: 2fr;
        }
    }
    .new2 {
        grid-row: 1/3;
        @include media($md) {
            max-width: 100%;
            grid-column: 1fr;
            grid-row: 4/6;
        }

        p {
            @include limit(2);
        }
    }
    .new3 {
        grid-row: 1/3;
        @include media($md) {
            max-width: 100%;
            grid-column: 1fr;
            grid-row: 4/6;
        }
        @include media($xs) {
            width: 100%;
            grid-column: 1fr;
            grid-row: 6/8;
        }

        p {
            @include limit(2);
        }
    }
    .new4 {
        grid-row: 4/6;
        @include media($md) {
            max-width: 100%;
            grid-column: 1fr;
            grid-row: 9/11;
        }
        @include media($xs) {
            grid-column: 1fr;
            grid-row: 11/13;
        }

        p {
            @include limit(2);
        }
    }
    .new5 {
        grid-row: 4/6;
        @include media($md) {
            max-width: 100%;
            grid-column: 1fr;
            grid-row: 9/11;
        }
        @include media($xs) {
            grid-column: 1fr;
            grid-row: 13/15;
        }
        
        p {
            @include limit(2);
        }
    }
    .new6 {
        grid-column: 3/5;
        grid-row: 3/6;
        @include media($md) {
            max-width: 100%;
            grid-column: 1/3;
            grid-row: 6/9;
        }
        @include media($xs) {
            grid-column: 1fr;
            grid-row: 8/11;
        }
    }
}

