.items {
    display: grid;
    gap: 30px;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: 318px;
    margin-bottom: 40px;
    @include media($lg) {
        grid-template-columns: repeat(3, 1fr);
    }
    @include media($sm) {
        grid-template-columns: repeat(2, 1fr);
    }
    @include media($xs) {
        grid-template-columns: repeat(1, 1fr);
    }

    &__box {
        background: $darkGrey;
        position: relative;
        text-align: center;
        border-radius: 8px;
    }
}