* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: #323232;
}

ul {
  list-style: none;
}

body {
  font-family: "Montserrat";
  background: #E5E5E5;
}

.container {
  max-width: 1140px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (max-width: 1140px) {
  .container {
    max-width: 992px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 576px;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 100%;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

::after, ::before {
  font-family: "fontawesome";
}

.icon-burger::before {
  content: '\f0c9';
  font-weight: 300;
}

.icon-down::before {
  content: '\f078';
  font-weight: 300;
}

.icon-search::before {
  content: '\f002';
  font-weight: 300;
}

.icon-clock::before {
  content: '\f017';
  font-weight: 300;
  padding-right: 10px;
}

.icon-vk::before {
  content: '\f189';
  font-size: 20px;
}

.btn {
  position: absolute;
  top: 0;
  right: 0;
  margin-top: 20px;
  margin-right: 27px;
  width: 50px;
  height: 50px;
  border-radius: 8px;
  color: #000000;
  outline: none;
  border: none;
  font-size: 19px;
}

@media (max-width: 576px) {
  .btn {
    font-size: 14px;
    width: 30px;
    height: 30px;
  }
}

.btn:hover i {
  transform: scale(1.4);
}

@media (max-width: 576px) {
  .btn:hover i {
    transform: scale(1.3);
  }
}

.btncart {
