.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 18px 0;
  align-items: center;
  position: relative;
  /* &__search {
        font-size: 18px;
        display: block;
        @include media($xs) {
            font-size: 14px;
        }
    
        &:hover {
            transform: scale(1.3);
            color: $black;
        }
    } */
}

.header__menu {
  position: relative;
  z-index: 9999;
  display: flex;
  flex-wrap: wrap;
  order: 1;
}

@media (max-width: 992px) {
  .header__menu {
    order: 2;
  }
}

.header__menu ul {
  margin: 0;
  padding: 0;
  background-color: #c4c4c4;
  border: 1px solid #323232;
  box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  display: none;
  transition: .5s;
  transform-origin: 50% 0 0;
  z-index: 9999;
}

.header__menu ul.active {
  position: absolute;
  display: block;
  z-index: 100;
  top: 30px;
  left: 0;
}

.header__menu ul li {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.header__menu ul li a {
  width: 90px;
  display: block;
  padding: 5px 10px;
}

.header__menu ul li a::before {
  content: '';
  display: inline-block;
  width: 32px;
  height: 25px;
  margin-right: 5px;
  vertical-align: sub;
}

.header__menu ul li a:hover {
  background-color: rgba(50, 50, 50, 0.2);
}

.header__down {
  background: none;
  border: none;
  color: #323232;
  font-size: 12px;
  outline: none;
  line-height: 20px;
}

@media (max-width: 576px) {
  .header__down {
    font-size: 8px;
  }
}

.header__en::before {
  background-image: url(https://www.countryflags.io/gb/flat/32.png);
}

.header__ru::before {
  background-image: url(https://www.countryflags.io/ru/flat/32.png);
}

.header__uz::before {
  background-image: url(https://www.countryflags.io/uz/flat/32.png);
}

.header__selectedlang {
  cursor: pointer;
  display: flex;
  flex-wrap: wrap;
  line-height: 2;
}

@media (max-width: 576px) {
  .header__selectedlang {
    font-size: 12px;
  }
}

.header__selectedlang::before {
  content: '';
  display: block;
  width: 32px;
  height: 25px;
  background-image: url(https://www.countryflags.io/ru/flat/32.png);
}

@media (max-width: 576px) {
  .header__selectedlang::before {
    display: none;
  }
}

.header__link {
  display: block;
  order: 2;
}

@media (max-width: 992px) {
  .header__link {
    flex-basis: 100%;
    order: 1;
    text-align: center;
    margin-bottom: 15px;
  }
}

.header__title {
  font-size: 32px;
  font-weight: 700;
  line-height: 23px;
}

.header #wrap {
  display: block;
  position: relative;
  float: right;
  padding: 0;
  position: relative;
  height: 25px;
  order: 3;
}

.header__textinp {
  height: 25px;
  font-size: 18px;
  display: inline-block;
  font-weight: 400;
  border: none;
  outline: none;
  color: #323232;
  padding: 3px;
  padding-right: 25px;
  width: 0;
  position: absolute;
  top: 0;
  right: 0;
  background: none;
  z-index: 3;
  transition: width 0.4s cubic-bezier(0, 0.795, 0, 1);
  cursor: pointer;
}

@media (max-width: 576px) {
  .header__textinp {
    font-size: 12px;
  }
}

.header__textinp:focus:hover {
  border-bottom: 1px solid #c4c4c4;
}

.header__textinp:focus {
  width: 250px;
  z-index: 1;
  border-bottom: 1px solid #c4c4c4;
  cursor: text;
}

@media (max-width: 576px) {
  .header__textinp:focus {
    width: 130px;
  }
}

.header__researcher {
  height: 25px;
  width: 25px;
  display: block;
  float: right;
  border: none;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  cursor: pointer;
  opacity: 0.4;
  cursor: pointer;
  transition: opacity .4s ease;
  background: none;
}

.header__researcher:hover {
  opacity: 0.8;
}

.header__researcher:focus {
  outline: none;
}

.header__searchicon {
  position: absolute;
  right: 4px;
  top: 3px;
  font-size: 18px;
  color: #323232;
  z-index: 5;
  background: #E5E5E5;
}

.nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  position: relative;
  align-items: center;
}

.nav::before {
  content: '';
  position: absolute;
  top: 0;
  display: block;
  width: 100%;
  height: 1px;
  background-color: #c4c4c4;
}

.nav::after {
  content: '';
  position: absolute;
  bottom: 0;
  display: block;
  width: 100%;
  height: 1px;
  background-color: #c4c4c4;
}

.nav__burger {
  background: none;
  border: none;
  color: #323232;
  font-size: 30px;
  outline: none;
  line-height: 20px;
  padding: 10px 5px;
  display: none;
}

@media (max-width: 992px) {
  .nav__burger {
    display: block;
  }
}

.nav .menu {
  display: flex;
  flex-wrap: wrap;
  padding: 20px 0;
  z-index: 9998;
}

@media (max-width: 992px) {
  .nav .menu {
    position: absolute;
    left: 0;
    top: 40px;
    background: #c4c4c4;
    flex-direction: column;
    padding: 0;
    min-width: 150px;
    transform: scaleY(0);
    transition: .5s;
    transform-origin: 50% 0 0;
    opacity: 0;
  }
  .nav .menu.active {
    transform: scaleY(1);
    opacity: 1;
    background: rgba(0, 0, 0, 0.95);
    border-radius: 5px;
  }
}

@media (max-width: 992px) {
  .nav .menu li:hover {
    background: rgba(255, 255, 255, 0.3);
  }
}

.nav .menu__link {
  margin-right: 50px;
  display: block;
  font-size: 16px;
  position: relative;
  padding-bottom: 5px;
}

@media (max-width: 992px) {
  .nav .menu__link {
    padding: 10px 10px;
    color: #FFFFFF;
  }
}

.nav .menu__link::after {
  content: '';
  width: 0;
  height: 1px;
  background: #323232;
  position: absolute;
  bottom: 0;
  left: 50%;
  transition: .5s;
}

@media (max-width: 992px) {
  .nav .menu__link::after {
    left: 0;
    height: 0;
    width: 1px;
    bottom: 50%;
    background: #FFFFFF;
  }
}

.nav .menu__link:hover::after {
  width: 100%;
  left: 0;
}

@media (max-width: 992px) {
  .nav .menu__link:hover::after {
    width: 1px;
    height: 100%;
    bottom: 0;
  }
}

.nav .iconsmenu {
  display: flex;
  flex-wrap: wrap;
}

.nav__iconslink {
  margin-left: 30px;
  font-size: 18px;
  display: block;
}

.nav__iconslink:hover {
  transform: scale(1.3);
  color: #000000;
}

