.content {
    padding: 40px 0;
    min-height: 100vh;
    display: grid;
    gap: 30px;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: 385px;

    &__box1 {
        background: $darkGrey;
        border-radius: 8px;
        grid-column: 1/3;
        grid-row: 1/3;
        position: relative;
        text-align: center;
        position: relative;
        // z-index: -1;
        @include media($md) {
            grid-column: 1/4;
            grid-row: 1/3;
        }
        @include media($xs) {
            grid-column: 1/4;
            grid-row: 1/2;
        }
    }

    &__imgBig {
        @include media($xs) {
            display: none;
        }
    }

    &__normalizeImg {
        display: none;
        @include media($xs) {
            display: block;
        }
    }

    &__pack {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 197px;
        @include media($xs) {
            margin-top: 22px;
            flex-direction: column;
        }
    }

    &__info1 {
        margin-left: 40px;
        @include media($xs) {
            margin-left: 0;
        }
    }

    &__box2 {
        background: $darkGrey;
        border-radius: 8px;
        position: relative;
        text-align: center;
        @include media($md) {
            grid-row: 3/4;
            grid-column: 1/3;
        }
        @include media($sm) {
            grid-row: 3/4;
            grid-column: 1/4;
        }
        @include media($xs) {
            grid-row: 2/3;
            grid-column: 1/4;
        }
    }

    &__box3 {
        background: $darkGrey;
        border-radius: 8px;
        position: relative;
        text-align: center;  
        @include media($sm) {
            grid-row: 4/5;
            grid-column: 1/4;
        }
        @include media($xs) {
            grid-row: 3/4;
            grid-column: 1/4;
        }
    }
}