.header {
    @include row(0);
    justify-content: space-between;
    padding: 18px 0;
    align-items: center;
    position: relative;

    &__menu {
        position: relative;
        z-index: 9999;
        @include row(0);
        order: 1;
        @include media($md) {
            order: 2;
        }

        ul {
            margin: 0;
            padding: 0;
            background-color: $darkGrey;
            border: 1px solid $dark;
            box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.2);
            border-radius: 5px;
            display: none;
            transition: .5s;
            transform-origin: 50% 0 0;
            z-index: 9999;
            
            &.active {
                position: absolute;
                display: block;
                z-index: 100;
                top: 30px;
                left: 0; 
            }

            li {
                @include row(0);
                justify-content: space-between;
                                
                a {
                    width: 90px;
                    display: block;
                    padding: 5px 10px;
                    

                    &::before {
                        content: '';
                        display: inline-block;
                        width: 32px;
                        height: 25px;
                        margin-right: 5px;
                        vertical-align: sub;
                    }

                    &:hover {
                        background-color: rgba($dark, 0.2);
                    }
                }
            }
        }
    }

    &__down {
        background: none;
        border: none;
        color: $dark;
        font-size: 12px;
        outline: none;
        line-height: 20px;
        @include media($xs) {
            font-size: 8px;
        }
    }

    &__en {
        &::before {
            background-image: url(https://www.countryflags.io/gb/flat/32.png);
        }
    }
    &__ru {
        &::before {
            background-image: url(https://www.countryflags.io/ru/flat/32.png);
        }
    }
    &__uz {
        &::before {
            background-image: url(https://www.countryflags.io/uz/flat/32.png);
        }
    }

    &__selectedlang {
        cursor: pointer;
        @include row(0);
        line-height: 2;   
        @include media($xs) {
            font-size: 12px;
        }

        &::before {
            content: '';
            display: block;
            width: 32px;
            height: 25px;
            background-image: url(https://www.countryflags.io/ru/flat/32.png);
            @include media($xs) {
                display: none;
            }
        }
    }

    &__link {
        display: block;
        order: 2;
        @include media($md) {
            flex-basis: 100%;
            order: 1;
            text-align: center;
            margin-bottom: 15px;
        }
    }

    &__title {
        font-size: 32px;
        font-weight: 700;
        line-height: 23px;
    }

    /* &__search {
        font-size: 18px;
        display: block;
        @include media($xs) {
            font-size: 14px;
        }
    
        &:hover {
            transform: scale(1.3);
            color: $black;
        }
    } */


    #wrap {
        display: block;
        position: relative;
        float: right;
        padding: 0;
        position: relative;
        height: 25px;
        order: 3;
    }

    &__textinp {
        height: 25px;
        font-size: 18px;
        display: inline-block;
        font-weight: 400;
        border: none;
        outline: none;
        color: $dark;
        padding: 3px;
        padding-right: 25px;
        width: 0;
        position: absolute;
        top: 0;
        right: 0;
        background: none;
        z-index: 3;
        transition: width .4s cubic-bezier(0.000, 0.795, 0.000, 1.000);
        cursor: pointer;
        @include media($xs) {
            font-size: 12px;
        }
        
        &:focus:hover {
            border-bottom: 1px solid $darkGrey;
        }

        &:focus {
            width: 250px;
            z-index: 1;
            border-bottom: 1px solid $darkGrey;
            cursor: text;
            @include media($xs) {
                width: 130px;
            }
        }
    }

    &__researcher {
        height: 25px;
        width: 25px;
        display: block;
        float: right;
        border: none;
        position: absolute;
        top: 0;
        right: 0;
        z-index: 2;
        cursor: pointer;
        opacity: 0.4;
        cursor: pointer;
        transition: opacity .4s ease;
        background: none;
        &:hover {
            opacity: 0.8;
        }
        &:focus {
            outline: none;
        }
    }

    &__searchicon {
        position: absolute;
        right: 4px;
        top: 3px;
        font-size: 18px;
        color: $dark;
        z-index: 5;
        background: $main;
    }
}

.nav {
    @include row(0);
    justify-content: space-between;
    position: relative;
    align-items: center;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        display: block;
        width: 100%;
        height: 1px;
        background-color: $darkGrey;
    }

    &::after {
        content: '';
        position: absolute;
        bottom: 0;
        display: block;
        width: 100%;
        height: 1px;
        background-color: $darkGrey;
    }

    &__burger {
        background: none;
        border: none;
        color: $dark;
        font-size: 30px;
        outline: none;
        line-height: 20px;
        padding: 10px 5px;
        display: none;
        @include media($md) {
            display: block
        }
    }

    .menu {
        @include row(0);
        padding: 20px 0;
        z-index: 9998;
        @include media($md) {
            position: absolute;
            left: 0;
            top: 40px;
            background: $darkGrey;
            flex-direction: column;
            padding: 0;
            min-width: 150px;
            transform: scaleY(0);
            transition: .5s;
            transform-origin: 50% 0 0;
            opacity: 0;
            &.active {
                transform: scaleY(1);
                opacity: 1;
                background: rgba(0, 0, 0, 0.95);
                border-radius: 5px;
            }
        }

        li {
            &:hover {
                @include media($md) {
                    background: rgba($white, 0.3);
                }
            }
        }

        &__link {
            margin-right: 50px;
            display: block;
            font-size: 16px;
            position: relative;
            padding-bottom: 5px;
            @include media($md) {
                padding: 10px 10px;
                color: $white;
            }

            &::after {
                content: '';
                width: 0;
                height: 1px;
                background: $dark;
                position: absolute;
                bottom: 0;
                left: 50%;
                transition: .5s;
                @include media($md) {
                    left: 0;
                    height: 0;
                    width: 1px;
                    bottom: 50%;
                    background: $white;
                }
            }

            &:hover {

                &::after {
                    width: 100%;
                    left: 0;
                    @include media($md) {
                        width: 1px;
                        height: 100%;
                        bottom: 0;
                    }
                }
            }
        }
    }
    .iconsmenu {
        @include row(0)
    }
    &__iconslink {
        margin-left: 30px;
        font-size: 18px;
        display: block;
    
        &:hover {
            transform: scale(1.3);
            color: $black;
        }
    }
}

