* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
    color: $dark;
}

ul {
    list-style: none;
}

body {
    font-family: "Montserrat";
    background: $main;
}

.container {
    @include container();
}

.row {
    @include row();
}

::after, ::before {
    font-family: "fontawesome";
}

.icon {
    &-burger::before {
        content: '\f0c9';
        font-weight: 300;
    }

    &-down::before {
        content: '\f078';
        font-weight: 300;
    }

    &-search::before {
        content: '\f002';
        font-weight: 300;
    }

    &-clock::before {
        content: '\f017';
        font-weight: 300;
        padding-right: 10px;
    }

    &-vk::before {
        content: '\f189';
        font-size: 20px;
    }
}

.btn {
    position: absolute;
    top: 0;
    right: 0;
    margin-top: 20px;
    margin-right: 27px;
    width: 50px;
    height: 50px;
    border-radius: 8px;
    color: $black;
    outline: none;
    border: none;
    font-size: 19px;
    
    @include media($xs) {
        font-size: 14px;
        width: 30px;
        height: 30px;
    }
        
    &:hover {
        i {
            transform: scale(1.4);
            @include media($xs) {
                transform: scale(1.3);
            }
        }
    }
}

.btncart {
    margin-top: 85px;
    @include media($xs) {
        margin-top: 60px;
    }
}

.sales {
    position: absolute;
    top: 0;
    left: 0;
    padding: 10px 30px;
    background: $dark;
    color: $white;
    margin-top: 20px;
    margin-left: 13px;
    border-radius: 8px;
    font-weight: 400;
    font-size: 20px;
    @include media($xs) {
        padding: 10px 20px;
    }
}

.pack {
    margin-top: 22px;
    .info {
        h3 {
            font-weight: 700;
            font-size: 20px;
            margin-bottom: 16px;
            margin-top: 32px;
            color: $black;
        }
    
        p {
            font-size: 18px;
            line-height: 22px;
            font-weight: 400;
            color: $black;
        }
    }
}

.title {
    padding-top: 60px;
    padding-bottom: 100px;
    font-size: 22px;
    text-transform: uppercase;
    line-height: 27px;
    color: $black;
    text-align: center;
    font-weight: 400;
}

