footer {
    background: $white;
    
    .footer {
        padding: 65px 15px;
        @include row(0);
        @include media($lg) {
            justify-content: space-around;
        }
        @include media($md) {
            justify-content: space-around;
            text-align: center;
        }

        &__title {
            font-size: 18px;
            line-height: 22px;
            letter-spacing: 3px;
            font-weight: 400;
            color: $dark;
            margin-bottom: 40px;
        }

        &__items {
            margin-right: 111px;
            @include media($lg) {
                margin-right: 60px;
            }
            @include media($md) {
                margin-right: 30px;
            }
            @include media($xs) {
                margin-bottom: 30px;
            }
        }

        &__list {
            padding-bottom: 10px;
        }

        &__link {
            font-size: 18px;
            line-height: 13px;
            color: $black;
            position: relative;
            transition: .5s;
            &::after {
                content: '';
                background: $dark;
                width: 0;
                height: 1px;
                position: absolute;
                bottom: 0;
                left: 50%;
                transition: .5s;
            }
            &:hover {
                color: $dark;
                &::after {
                    width: 100%;
                    left: 0;
                }
            }
        }

        &__news {
            margin-right: 144px;
            @include media($md) {
                margin-right: 0;
            }
        }

        &__info {
            h5 {
                margin-bottom: 22px;
            }
            @include media($xs) {
                margin-top: 30px;
            }

        }

        &__infolink {
            display: inline-block;
            margin: 15px 10px 15px 0;
        }

        &__social {
            margin-top: 25px;
            margin-bottom: 20px;
        }

        &__icon {
            color: $black;
            margin-right: 15px;
        }

        .vk{
            transition: .5s;
            &:hover {
                color: rgb(98, 98, 161);
            }
        }
        .youtube{
            transition: .5s;
            &:hover {
                color: rgb(212, 23, 23);
            }
        }
        .instagram{
            transition: .5s;
            &:hover {
                color: rgba(229, 14, 236, 0.63);
            }
        }
        .facebook{
            transition: .5s;
            &:hover {
                color: rgb(98, 98, 161);
            }
        }
    }
}